import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/product-attribute';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class ProductAttributesService {
  constructor(private http: HttpClient) {}

  productAttributes = new BehaviorSubject<[ProductAttribute]>(null);

  getAll() {
    return this.http.get(baseUrl + '/product-attribute').subscribe((res) => {
      if (res['data']) {
        this.productAttributes.next(res['data']);
      }
    });
  }

  getOne(itemId) {
    console.log(itemId);
    return this.http.get(baseUrl + `/product-attribute/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/product-attribute', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/product-attribute/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/product-attribute/${itemId}`);
  }
}
