import { Injectable } from '@angular/core';
import { HttpClient } from '@angular/common/http';
import { BehaviorSubject } from 'rxjs';
import '../../models/category';

import { baseUrl } from '../../config/variables';

@Injectable({
  providedIn: 'root',
})
export class CategoriesService {
  constructor(private http: HttpClient) {}

  categories = new BehaviorSubject<[Category]>(null);
  baseCategories = new BehaviorSubject<[Category]>(null);

  getBaseCategories() {
    return this.http.get(baseUrl + '/category/getBaseCategories').subscribe(
      (res) => {
        if (res['data']) {
          this.baseCategories.next(res['data']);
        }
      },
      (error) => {
        console.log(error);
        // this.error.next(error.error.message);
      }
    );
  }

  getAll() {
    return this.http.get(baseUrl + '/category').subscribe(
      (res) => {
        if (res['data']) {
          this.categories.next(res['data']);
        }
      },
      (error) => {
        console.log(error);
        // this.error.next(error.error.message);
      }
    );
  }

  getOne(itemId) {
    return this.http.get(baseUrl + `/category/${itemId}`);
  }

  addOne(form) {
    return this.http.post(baseUrl + '/category', form);
  }

  updateOne(form, itemId) {
    return this.http.put(baseUrl + `/category/${itemId}`, form);
  }

  deleteOne(itemId) {
    return this.http.delete(baseUrl + `/category/${itemId}`);
  }

  addSubCategory(form, parentId) {
    return this.http.post(baseUrl + `/category/${parentId}`, form);
  }
}
